html {
  overflow-x: hidden;
}

.col-md-6 {
  padding-right: 0px;
  padding-left: 0px;
}

::selection {
  color:#fff;
  background:#4bbf73;
}

::-moz-selection {
  color:#fff;
  background:#4bbf73;
}

::-webkit-selection {
  color:#fff;
  background:#4bbf73;
}

.list-group-item button {
  z-index: 2!important;
}

.btn.btn-secondary.wave-effect {
  position: relative;
  background-color: rgba(176, 255, 159, 0.51);
  width: 90px;
  overflow: hidden;
  z-index: 10;
}

.btn.btn-secondary.wave-effect:hover {
  background-color: rgba(224, 252, 178, 0.49);
}

.pulse {
  position:absolute;
  opacity: 0;
  border-radius: 100px;
  width: 100px;
  height: 100px;
  background-color: #50EE29;
  animation: pulse 0.6s;
  z-index: -1;
  filter: blur(3px);
  -webkit-filter: blur(3px);
  -moz-filter: blur(3px);
  -o-filter: blur(3px);
  -ms-filter: blur(3px);
}

@keyframes pulse {
  from { transform: scale(0); }
  50% { opacity: 0.5; }
  to { transform: scale(2); }
}

div.list {
  overflow: hidden;
  overflow-y: scroll;
  height: 80.9vh;
}

div.list.disabled {
  overflow-y: hidden;
}

@media screen and (max-width: 991px) {
  div.list {
    height: auto;
    overflow:inherit;
    margin-bottom: 20px;
  }
}

::-webkit-scrollbar {
  width: 2px;
}

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px #B8D4A8;
}

::-webkit-scrollbar-thumb {
    -webkit-box-shadow: inset 0 0 6px #4bbf73;
}

.header {
  text-align: center;
}

.list-group {
  color: black;
  padding: 0;
}

.badge.badge-success {
  border-radius: 10px;
  padding: 4px 7px 3px 7px;
  background-color: #bfbfbfbf;
  color: #fff;
  margin-left: 8px;
}

.list-group-item.item,
.list-group-item.header {
  padding: 0;
  display: flex;
}

.list-group-item.item p,
.list-group-item.header p {
  padding-top: 12px;
  padding-left: 22px;
  width: 80%;
  margin-bottom: 0;
}

.list-group-item.header p {
  margin-bottom: 10px;
}

.list-group .header {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  text-align: left;
  background-color: #f4fcef;
}

.list-group .footer {
  border: 0;
  border-top: solid 1px #dfdfdf;
}

.list-group .item:hover {
  cursor: pointer;
  background-color: #f4fcef;
}

.search {
  margin-top: 10px;
  width: 111px;
  margin-right: 20px;
  height: 25px;
  border:0;
  background-color: #f4fcef;
  border-bottom: solid 1px #dfdfdf;
}

.search:focus {
  outline: 0;
}

.list-group-item.item:hover,
.list-group-item.item:hover button.list{
  transition: 0.1s;
  background-color: #f4fcef;
}

.list-group-item button:hover {
  background-color: #DAE4D3!important;
}

.list-group-item button.disabled:hover {
  background-color: rgba(0, 0, 0, 0)!important;
}

button.header {
  background-color: #f4fcef!important;
}

input.rename {
  padding-left: 20px;
  width: 80%;
  border: 0;
}

input.rename:focus {
  outline: 0;
}

.over_li {
   list-style-type: none;
}

.over_li.hide {
  display: none;
}

.under_ul {
  padding: 0;
  margin: 0;
  width: 100%;
}

.list-group-item.item.editCategoryInput {
  display: none;
}

.fas.fa-fire, .fas.fa-bookmark {
  margin-right: 3px;
  height: 29px;
}

.fas.fa-fire {
  color: #EE652A;
}

p .fas.fa-bookmark {
  color: #8E19A6;
}

button .fas.fa-bookmark {
  color: #8E19A6;
  height: 0px;
}

.list-group-item.item form {
 width: 100%;
 display: flex;
}

.list-group-item.item form input {
  background-color: white;
  padding-left: 21px;
}

.list-group-item.item form input:focus {
  box-shadow: 0 0 0 #fff;
}

.form-group.categories {
  margin: 0;
  width: 90%;
}

input.danger {
  box-shadow: inset 0 0 0px 0px red;
  animation-name: danger;
  animation-duration: 0.3s;
  animation-iteration-count: infinite;
}

textarea.danger {
  box-shadow: inset 0 0 0px 0px red;
  animation-name: danger;
  animation-duration: 0.3s;
  animation-iteration-count: infinite;
}

@keyframes danger {
  from {  box-shadow: inset 0 0 0px 0px red; }
  50% {  box-shadow: inset 0 0 5px 0px red; }
  to { box-shadow: inset 0 0 0px 0px red; }
}

p.empty {
  padding-bottom: 12px;
}

.list-group-item.header button.head {
  background-color: #f4fcef;
}

.question-area textarea{
  width: 100%;
  border: 0;
  resize:none;
  height: 100px;
  border-right: solid 1px #CFCFCF;
  border-left: solid 1px #CFCFCF;
}


.question-area textarea:nth-child(2) {
  margin-top: -7px;
  margin-bottom: -6px;
  border-top: solid 1px #CFCFCF;
}

.question-area textarea:disabled{
  width: 100%;
  background-color: white;
  resize:none;
  height: 100px;
}

textarea:focus {
  outline: 0px;
}

.btn-group {
  border: solid 1px #E2E2E2;
  width: 100%;
  height: 47px;
  margin-top: -1px;
}

.question-bar-head .right{
  font-style: normal;
  padding: 12px 14px 10px 0;
}

.btn.btn-secondary {
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 4%;
  background-color: #fff;
  width: 10%;
  height: 43px;
}

.btn.btn-secondary:hover {
  background-color: #DAE4D3;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type='number'] {
  cursor: pointer;
  background-color: #fff;
  padding-left: 0;
  padding-right: 0;
    text-align: center;
    -moz-appearance: textfield;
}

a.crumb {
  font-weight: lighter;
  padding: 10px 0 10px 14px;
}

a.crumb:hover {
  text-decoration: underline;
}

a.crumb i {
  margin: 0;
  padding-right: 13px;
  margin-top: 6px;
}

button[data-title]:hover::after {
  position: absolute;
  padding: 11px;
  top:-46px;
  left: -75px;
  color: white;
  background-color: rgba(76, 191, 115, 0.75);
  width: 200px;
  content: attr(data-title);
  animation-name: tooltip;
  animation-duration: 1s;
}

.list-group-item button {
  position:relative;
  z-index: 9999;
}

@keyframes tooltip {
  0%,55% { opacity:0; transform: translateY(-1000px) }
  60% { transform: translateY(0) }
  to { opacity:1 }
}

textarea.you-answer:focus {
  box-shadow: 0 0 0 white;
}

textarea.you-answer.success {
  box-shadow: inset 0 0 12px 0px #4bbf73;
  animation: success 0.5s;
}

@keyframes success {
  0% { box-shadow: inset 0 0 0px 0px #4bbf73 }
  to { box-shadow: inset 0 0 12px 0px #4bbf73 }
}

.btn.btn-secondary.disabled {
  color: #D1DAD1;
  cursor: default;
  position:relative;
}

.btn.btn-secondary.disabled:focus {
  box-shadow: 0 0 0 #fff;
}

.btn.btn-secondary.disabled:hover {
  background-color: #fff;
}

.list-group-item.add-question p {
  padding-bottom: 10px;
}

.question-area textarea {
  margin-top: 1px;
  margin-bottom: 2px;
}


.modal_btn_group {
  max-width: 15%;
}

.col-lg-6.my {
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

button.modul_btn:active {
  height: 20px;
  border: 0;
  background: #DAE4D3;

}

button.modul_btn {
  height: 20px;
  border: 0;
  cursor: pointer;
  transition: 0.1s;
}

#modal_input {
  margin: 0% 0% 0% auto;
}

.modal {
  display: none;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 3;
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
  animation: modal 0.1s;
}

.modal-content {
  border-radius: 15px;
  background-color: #fefefe;
  padding-top: 20px;
  border: 1px solid #888;
  width: 40%;
  animation: modal-content 0.1s;
}

@media screen and (max-width: 769px) {
  .modal-content {
    margin: 5% auto;
    width: 90%;
  }
}

@keyframes modal {
  from {opacity: 0}
  to {opacity: 1}
}

@keyframes modal-content {
  from {
    transform: scale(2);
    opacity: 0
  }
  to {
    transform: scale(1);
    opacity: 1
  }
}

p.under-modal {
  text-align: center;
  padding: 0 20px 0 20px;
}

button.under-modal {
  border-top: solid 1px #EBE9E9;
}

button.under-modal:focus {
  box-shadow: 0 0 0 #fff;
}

button.under-modal.red {
  font-weight: bold;
  color: red;
}

.alert {
  position: fixed;
  z-index: 9999;
  width: 120%;
  animation-name: alert;
  animation-duration: 2s;
  transform:translateY(-50px);
  opacity: 0.9;
}

@keyframes alert {
  from {  transform:translateY(-50px); }
  10%,90% { transform:translateY(0px); }
  to { transform:translateY(-50px); }
}

.row {
  width: 100%;
  margin: 0;
}

.col-md-1 {
  padding-right: 0;
  padding-left: 0;
}

.col-md-5 {
  padding-top: 10px;
}

